/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-booking-form {
  max-width: 480px;
  margin: 0px auto; }
  .b-booking-form__fieldset {
    padding: 30px 15px 0px;
    margin-bottom: 50px; }
  .b-booking-form__note {
    color: #8c8c8c;
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 10px;
    font-family: "montserratregular"; }
  .b-booking-form__button:not(:last-child) {
    margin-bottom: 25px; }

@media (min-width: 768px) {
  .b-booking-form__button {
    width: 50%;
    display: inline-block; }
    .b-booking-form__button:first-child {
      padding-right: 10px; }
    .b-booking-form__button:last-child {
      padding-left: 10px; }
    .b-booking-form__button:not(:last-child) {
      margin-bottom: 0px; } }



