/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-cover-page {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 40;
  background-color: #000000;
  opacity: 0;
  width: 100%;
  height: 100%;
  touch-action: none;
  pointer-events: none;
  transition-property: opacity;
  transition-duration: 0.2s; }
  .b-cover-page.active {
    opacity: 0.8;
    touch-action: auto;
    pointer-events: all; }



