/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-booking-request {
  margin-left: -15px;
  margin-right: -15px;
  font-size: 0; }
  .b-booking-request__item {
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 30px;
    border-bottom: 1px solid #7c4dff;
    margin-bottom: 40px; }
  .b-booking-request__player-name {
    font-size: 18px;
    line-height: 26px; }
  .b-booking-request__player-tel {
    font-size: 16px;
    line-height: 1; }
  .b-booking-request__training {
    margin-top: 15px;
    margin-bottom: 20px; }
  .b-booking-request__training-playground {
    font-size: 15px;
    line-height: 21px;
    color: #0d6041; }
  .b-booking-request__training-time {
    font-size: 14px;
    line-height: 21px;
    color: #e94d51; }
  .b-booking-request__button-item:not(:last-child) {
    margin-bottom: 20px; }

@media (min-width: 768px) {
  .b-booking-request__item {
    display: inline-block;
    vertical-align: top;
    border: 1px solid #ebebeb;
    border-radius: 6px;
    padding: 20px;
    background-color: #fff;
    width: calc(50% - 30px);
    margin-left: 15px;
    margin-right: 15px; } }

@media (min-width: 1280px) {
  .b-booking-request__item {
    width: calc(100% / 3 - 30px); }
  .b-booking-request__player-name {
    font-size: 20px;
    line-height: 32px; }
  .b-booking-request__player-tel {
    font-size: 18px;
    line-height: 30px; }
  .b-booking-request__training-playground {
    font-size: 17px;
    line-height: 25px; }
  .b-booking-request__training-time {
    font-size: 16px;
    line-height: 25px; } }



