/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-trainer-booking-request {
  padding-top: 40px; }

@media (min-width: 768px) {
  .b-trainer-booking-request {
    background-color: #f4f8f9;
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 15px;
    padding-right: 15px; } }

@media (min-width: 1024px) {
  .b-trainer-booking-request {
    height: 100%; } }



