/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-hamburger {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: relative;
  z-index: 50;
  overflow: hidden;
  transition: border-color 0.2s, right 0.4s;
  right: 0; }
  .b-hamburger svg {
    width: 50px;
    position: absolute;
    top: -2px;
    left: -8px;
    z-index: 1; }
  .b-hamburger path {
    stroke-width: 40;
    stroke-linecap: round;
    stroke: #fff;
    fill: none;
    transition: all 0.4s ease-in-out, stroke 0.2s; }
  .b-hamburger__top-bar, .b-hamburger__bottom-bar {
    stroke-dasharray: 240px 910px; }
  .b-hamburger__middle-bar {
    stroke-dasharray: 240px 240px; }
  .no-touchevents .b-hamburger:hover,
  .touchevents .b-hamburger:active {
    border-color: #ffc860; }
    .no-touchevents .b-hamburger:hover path,
    .touchevents .b-hamburger:active path {
      stroke: #ffc860; }
  .b-hamburger.open {
    right: calc(-100% + 35px); }
  .b-hamburger.open .b-hamburger__top-bar, .b-hamburger.open .b-hamburger__bottom-bar {
    stroke-dashoffset: -650px; }
  .b-hamburger.open .b-hamburger__middle-bar {
    stroke-dasharray: 0px 220px;
    stroke-dashoffset: -120px; }

@media (min-width: 768px) {
  .b-hamburger.open {
    right: -360px; } }



