/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-list-trainer {
  padding-top: 35px; }
  .b-list-trainer__list {
    padding-left: 0px; }
  .b-list-trainer__item {
    list-style-type: none;
    border-bottom: 1px solid #7c4dff;
    margin: 0px -15px 30px;
    padding: 0px 15px 30px; }
  .b-list-trainer__more {
    padding-bottom: 50px;
    text-align: center; }
  .b-list-trainer__note {
    display: block;
    font-size: 12px;
    line-height: 1;
    letter-spacing: 0.02px;
    color: #8c8c8c; }

@media (min-width: 768px) {
  .b-list-trainer__list {
    margin-left: -15px;
    margin-right: -15px;
    display: flex;
    flex-wrap: wrap; }
  .b-list-trainer__item {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    padding: 0px 15px;
    margin-left: 0px;
    margin-right: 0px;
    border-bottom: none; } }

@media (min-width: 1280px) {
  .b-list-trainer__item {
    width: calc(100% / 3); } }



