/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-trainer-info__playground-list {
  padding-left: 0px;
  margin-top: 0px;
  margin-bottom: 25px; }

.b-trainer-info__playground-item {
  display: inline-block;
  list-style-type: none;
  width: 100%; }
  .b-trainer-info__playground-item:not(:last-child) {
    margin-bottom: 15px; }

.b-trainer-info__title-field {
  font-size: 14px;
  line-height: 19px;
  font-family: "montserratbold";
  margin-bottom: 5px; }
  @media (min-width: 1024px) {
    .b-trainer-info__title-field {
      font-size: 15px;
      line-height: 21px; } }
  @media (min-width: 1280px) {
    .b-trainer-info__title-field {
      font-size: 16px;
      line-height: 24px; } }
  .b-trainer-info__title-field--light {
    font-family: "montserratmedium"; }

.b-trainer-info__cost {
  font-size: 0; }

@media (min-width: 768px) {
  .b-trainer-info {
    margin: 0px -15px;
    padding: 50px 15px 0px;
    background-color: #f4f8f9; }
    .b-trainer-info__form {
      max-width: 480px;
      margin: 0px auto;
      background-color: #fff;
      padding: 30px;
      overflow: hidden;
      border-radius: 6px;
      border: 1px solid #ebebeb;
      margin-bottom: 50px; }
    .b-trainer-info__playground {
      margin-left: -15px;
      margin-right: -15px;
      padding: 20px 15px;
      background-color: #fff;
      margin-bottom: 50px;
      border-top: 1px solid;
      border-bottom: 1px solid;
      border-color: #ebebeb; }
    .b-trainer-info__playground-list {
      margin-left: -15px;
      margin-right: -15px; }
    .b-trainer-info__playground-item {
      padding-left: 15px;
      padding-right: 15px;
      width: 50%; }
    .b-trainer-info__button {
      text-align: center;
      padding-bottom: 50px; } }

@media (min-width: 1024px) {
  .b-trainer-info__playground-item {
    width: calc(100% / 3); } }

@media (min-width: 1280px) {
  .b-trainer-info__playground-item {
    width: 25%; } }



