/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-schedule-list {
  margin: 0px;
  padding-left: 0;
  padding-bottom: 15px; }
  .b-schedule-list__item {
    list-style-type: none; }
    .b-schedule-list__item:not(:last-child) {
      margin-bottom: 20px; }

@media (min-width: 768px) {
  .b-schedule-list__item {
    border: 1px solid #ebebeb;
    border-radius: 6px;
    overflow: hidden;
    transition: border-color 0.2s; }
    .no-touchevents .b-schedule-list__item--hover:hover,
    .touchevents .b-schedule-list__item--hover:active {
      border-color: #ffb72d; } }



