  padding-bottom: 20px;
  margin-bottom: 30px; }
  .b-setting-choose-day__title {
    font-size: 14px;
    line-height: 19px;
    font-family: "montserratbold";
    display: block;
    padding-right: 0;
    margin-bottom: 10px; }
