/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-search-list-playground {
  display: block;
  border: 1px solid #ebebeb;
  padding: 10px;
  border-radius: 5px;
  transition: border-color 0.2s;
  position: relative; }
  .b-search-list-playground--hover {
    cursor: pointer; }
    .no-touchevents .b-search-list-playground--hover:hover,
    .touchevents .b-search-list-playground--hover:active {
      border-color: #ffb72d; }
  .b-search-list-playground__input {
    display: none; }
  .b-search-list-playground__label {
    display: inline-block;
    vertical-align: text-bottom;
    width: 20px;
    height: 20px;
    padding: 3px;
    border-radius: 50%;
    border: 1px solid lightgray;
    background-color: transparent;
    transition-property: background-color;
    transition-duration: 0.2s; }
  .b-search-list-playground__icon-check {
    vertical-align: top;
    opacity: 0;
    transition: opacity 0.2s; }
  .b-search-list-playground__input:checked + .b-search-list-playground__label {
    background-color: #0d6041; }
    .b-search-list-playground__input:checked + .b-search-list-playground__label .b-search-list-playground__icon-check {
      opacity: 1; }
  .b-search-list-playground__content {
    display: inline-block;
    vertical-align: text-top;
    padding-left: 15px; }
  .b-search-list-playground__name {
    font-size: 15px;
    line-height: 18px;
    color: #0d6041; }
  .b-search-list-playground__address {
    font-size: 14px;
    line-height: 17px;
    color: #e94d51; }
  .b-search-list-playground__delete {
    cursor: pointer;
    display: block;
    position: absolute;
    width: 20px;
    right: 5px;
    top: 5px; }

@media (min-width: 1024px) {
  .b-search-list-playground__name {
    font-size: 16px;
    line-height: 21px; }
  .b-search-list-playground__address {
    font-size: 15px;
    line-height: 20px; } }



