/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-page-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  min-width: 320px; }
  .b-page-wrapper--overflow {
    overflow: hidden; }
  .b-page-wrapper.no-scroll, .ReactModal__Body--open .b-page-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden; }
  .b-page-wrapper.no-touch {
    pointer-events: none;
    touch-action: none; }



