/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-object-service {
  padding: 0px;
  margin-bottom: 30px; }
  .b-object-service__item {
    list-style-type: none;
    position: relative;
    padding: 6px 0px;
    border-bottom: 1px solid #ebebeb; }
    .b-object-service__item:not(:last-child) {
      margin-bottom: 20px; }
  .b-object-service__training-playground {
    font-size: 15px;
    line-height: 21px;
    color: #0d6041; }
  .b-object-service__playground-additional {
    font-size: 14px;
    line-height: 21px;
    color: #e94d51; }
  .b-object-service__info {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 60px);
    padding-left: 10px; }
    .b-object-service__info p {
      font-size: 15px;
      line-height: 21px;
      color: #0d6041; }
  .b-object-service__icon {
    display: inline-block;
    vertical-align: top;
    width: 60px;
    text-align: center;
    padding-top: 3px; }
    .b-object-service__icon::after {
      content: '';
      position: absolute;
      top: 6px;
      bottom: 6px;
      left: 60px;
      border-right: 1px solid #ebebeb; }
    .b-object-service__icon svg {
      fill: #F55F0C; }

@media (min-width: 1280px) {
  .b-object-service__training-playground {
    font-size: 17px;
    line-height: 26px; }
  .b-object-service__playground-additional {
    font-size: 16px;
    line-height: 26px; } }



