/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-modal__title {
  margin-top: 0px; }

.b-modal__title-group {
  font-family: "montserratbold";
  font-size: 18px;
  color: #7c4dff; }

.ReactModal__Content {
  top: 0px !important;
  left: 0px !important;
  right: 0px !important;
  bottom: 0px !important;
  padding: 45px 15px !important;
  max-width: 700px;
  margin: 0px auto; }
  .ReactModal__Content::before {
    content: '';
    background: #fff;
    position: sticky;
    margin-top: -45px;
    top: -45px;
    left: 0px;
    display: block;
    height: 45px; }

.ReactModal__Overlay {
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.8) !important; }



