/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-button {
  background-color: #ffb72d;
  border: none;
  background: linear-gradient(to right, #68a2b8, #55bc96);
  border-radius: 8px;
  font-family: "montserratbold";
  color: #000;
  padding: 15px 30px;
  transition: color 0.2s;
  font-size: 14px;
  display: inline-block;
  text-align: center; }
  .b-button:focus {
    outline: none; }
  .no-touchevents .b-button:hover,
  .touchevents .b-button:active {
    color: #000;
    background-color: #0d60412c; }
  .b-button--full {
    width: 100%; }
  .b-button--orange {
    background: none;
    border: 1px solid #0d6041; }
  .b-button--save {
    width: 100%;
    border-radius: 0;
    padding: 12px 30px; }
  .b-button--hello-page {
    width: 260px; }

@media (min-width: 1280px) {
  .b-button {
    font-size: 16px; } }



