/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-close {
  position: relative;
  cursor: pointer;
  display: inline-block;
  width: 32px;
  height: 32px;
  padding: 0px;
  border: 0px;
  background: none;
  border-radius: 50%;
  background-color: #ffb72d;
  border: 2px solid transparent;
  transition: border-color 0.2s; }
  .no-touchevents .b-close:hover {
    border-color: #fff; }
  .b-close:before, .b-close:after {
    position: absolute;
    top: 5px;
    left: 13px;
    content: ' ';
    height: 18px;
    width: 2px;
    background-color: #fff; }
  .b-close:before {
    transform: rotate(45deg); }
  .b-close:after {
    transform: rotate(-45deg); }
  .b-close--modal {
    position: fixed;
    top: 10px;
    right: 20px; }



