  transform: translateX(-100%);
  transition-property: transform;
  transition-duration: 0.2s; }
  .b-profile-tabs__list-wrapper.open {
    transform: translateX(0%); }

.b-profile-tabs__list {
  margin: 0px;
  padding: 0; }

.b-profile-tabs__back {
