/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-cost-information {
  font-size: 14px;
  font-family: "montserratmedium";
  margin-bottom: 25px; }
  .b-cost-information__title {
    color: #0d6041;
    line-height: 18px;
    margin-bottom: 5px; }
  .b-cost-information__cost {
    padding: 4px 4px 2px;
    border: 1px solid #eee;
