/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-profile-tab {
  list-style-type: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 40px; }
  .b-profile-tab.active .b-profile-tab__text {
    color: #0d6041; }
  .b-profile-tab__icon {
    width: 30px;
    flex-shrink: 0;
    margin-right: 20px; }
  .b-profile-tab__text {
    display: inline-block;
    margin: 0;
    font-size: 14px;
    transition: color 0.2s; }

@media (min-width: 1024px) {
  .b-profile-tab {
    border: 2px solid transparent;
    transition: border-color 0.2s;
    padding: 8px;
    margin-bottom: 25px; }
    .b-profile-tab.active {
      border-color: #0d6041; }
    .b-profile-tab__icon {
      max-width: 100%;
      width: auto;
      margin: 0px; }
    .b-profile-tab__text {
      display: none; } }



