/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-trainer-add-schedule__calendar {
  margin-left: -15px;
  margin-right: -15px;
  margin-bottom: 20px; }

.b-trainer-add-schedule__add-more {
  text-align: center;
  padding: 5px 0px 30px; }

.b-trainer-add-schedule__save {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 10; }

.b-trainer-add-schedule__schedule {
  padding-bottom: 38px; }

@media (min-width: 768px) {
  .b-trainer-add-schedule {
    margin: 0px -15px;
    padding: 0px 15px;
    background-color: #f4f8f9; } }

@media (min-width: 1024px) {
  .b-trainer-add-schedule__save {
    width: calc(100% - 85px);
    left: auto;
    right: 0px; } }

@media (min-width: 1280px) {
  .b-trainer-add-schedule {
    display: flex;
    min-height: 100%; }
    .b-trainer-add-schedule__calendar {
      margin: 0px -15px 0px 0px;
      order: 1;
      flex-shrink: 0;
      background-color: #7c4dff; }
      .b-trainer-add-schedule__calendar .b-date-calendar {
        position: sticky;
        top: 0; }
    .b-trainer-add-schedule__schedule {
      width: 100%;
      padding-right: 15px;
      padding-top: 20px; }
    .b-trainer-add-schedule__save {
      width: calc(100% - 405px);
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      margin-left: -117px;
      max-width: 1035px; } }



