/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-menu {
  position: fixed;
  z-index: 50;
  top: 0px;
  left: 0px;
  height: 100%;
  width: calc(100% - 60px);
  background-color: #0d6041;
  transform: translateX(-100%);
  transition-property: transform;
  transition-duration: 0.4s; }
  .b-menu.open {
    transform: translateX(0%); }
  .b-menu__list {
    padding: 60px 15px 0px; }
  .b-menu__item {
    list-style-type: none;
    margin-bottom: 10px; }
  .b-menu__link {
    font-family: "montserratmedium";
    font-size: 15px;
    line-height: 1;
    padding: 10px 0px;
    display: block;
    color: #fff; }

@media (min-width: 768px) {
  .b-menu {
    max-width: 360px; }
    .b-menu__link {
      font-size: 16px; } }

@media (min-width: 1024px) {
  .b-menu__link {
    font-size: 18px; }
  .b-menu__item--cabinet {
    display: none; } }

@media (min-width: 1440px) {
  .b-menu__link {
    font-size: 18px; } }

@media (min-width: 1680px) {
  .b-menu__link {
    font-size: 20px; } }



