/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-registration {
  background: linear-gradient(to bottom right, #68a2b8, #55bc96);
  overflow: auto;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%; }
  .b-registration .container {
    display: flex;
    min-height: 100%;
    padding-top: 25px;
    padding-bottom: 25px; }
  .b-registration__form {
    background: linear-gradient(to bottom right, #000000, #272727);
    margin: auto;
    padding: 40px 30px 60px;
    width: 100%;
    max-width: 370px;
    border-radius: 20px; }
  .b-registration__button-wrapper {
    text-align: center;
    position: relative; }
  .b-registration__error {
    font-family: "montserratbold";
    color: #ff0b0b;
    font-size: 14px;
    line-height: 1.3;
    position: absolute;
    padding-top: 15px;
    top: 100%;
    left: 0px;
    width: 100%; }

@media (min-width: 768px) {
  .b-registration__form {
    padding: 60px 45px 90px; }
  .b-registration__error {
    font-size: 16px;
    left: 0px;
    width: 100%; } }



