/** New 2019 colors */
:root {
  --accent: #F55F0C; }

.b-header {
  flex-shrink: 0;
  background-color: #0d6041;
  font-size: 0;
  padding-top: 10px;
  padding-bottom: 10px; }
  .b-header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative; }
  .b-header__get-schedule {
    font-family: "montserratmedium";
    font-size: 15px;
    line-height: 1;
    display: inline-block;
    padding-top: 3px;
    padding-bottom: 3px;
    color: #fff; }

@media (min-width: 768px) {
  .b-header__get-schedule {
    font-size: 16px; } }

@media (min-width: 1024px) {
  .b-header__get-schedule {
    font-size: 18px; } }

@media (min-width: 1680px) {
  .b-header__get-schedule {
    font-size: 20px; } }



